@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Tooltip container */
.Tooltip {
    width: 150px;
	height: 50px;
	position: relative;
	font-family: "Montserrat", sans-serif;
	line-height: 50px;
	text-align: center;
	color: ivory;
}

/* Tooltip Front Visible Portion and  Popup */
.Tooltip__Front,
.Tooltip__Back {
    position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition: 0.5s ease;
}

/* Tooltip Main Button */
.Tooltip__Front {
    background-color: #005aa7;
	z-index: 1;
	font-weight: 700;
	cursor: pointer;
}

/* Tooltip Content */
.Tooltip__Back {
    background-color: #e2fdff;
	box-sizing: border-box;
	color: #363638;
	padding: 0.75rem;
	line-height: calc(50px - 1.5rem - 10px);
	font-size: 0.9rem;
    border: 5px solid #005aa7;
}

/* Tooltip pops out to right */
.right:hover .Tooltip__Front {
	transform: translateX(-20%);
	letter-spacing: 1px;
	border-top-left-radius: 1rem;
	border-bottom-left-radius: 1rem;
}

.right .Tooltip__Back {
	text-align: right;
	border-top-right-radius: 1rem;
	border-bottom-right-radius: 1rem;
}
.right:hover .Tooltip__Back {
	transform: translateX(30%);
}

/* Tooltip pops out to left */
.left:hover .Tooltip__Front {
	transform: translateX(20%);
	letter-spacing: 1px;
	border-top-right-radius: 1rem;
	border-bottom-right-radius: 1rem;
}

.left .Tooltip__Back {
	text-align: left;
	border-top-left-radius: 1rem;
	border-bottom-left-radius: 1rem;
}
.left:hover .Tooltip__Back {
    transform: translateX(-30%);
}

/* Tooltip pops out above */
.top:hover .Tooltip__Front {
	transform: translateY(25%);
	letter-spacing: 1px;
	border-bottom-left-radius: 1rem;
	border-bottom-right-radius: 1rem;
}

.top .Tooltip__Back {
	text-align: center;
	padding-top: 10px;
	border-top-left-radius: 1rem;
	border-top-right-radius: 1rem;
}
.top:hover .Tooltip__Back {
	transform: translateY(-50%);
}

/* Tooltip pops out below */
.bottom:hover .Tooltip__Front {
	transform: translateY(-25%);
	letter-spacing: 1px;
	border-top-left-radius: 1rem;
	border-top-right-radius: 1rem;
}

.bottom .Tooltip__Back {
	text-align: center;
	padding-top: 15px;
	border-bottom-left-radius: 1rem;
	border-bottom-right-radius: 1rem;
}
.bottom:hover .Tooltip__Back {
	transform: translateY(50%);
}

/* Layout Container  */
.Layout{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #7a7e71;
    font-size: 1.2rem;
}

/* Dropdown List */
.PositionDropdown select{
    padding:  0 0.5rem;
    font-size: inherit;
    background-color: inherit;
    font-family: inherit;
}

